import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { User } from '../../index';
import { MapsService } from '../../services/maps/maps.service'
import { Subject, Observable } from 'rxjs'

@Injectable({
  providedIn: 'root'
})
export class UserService {
  private users: any = []
  constructor(private http: HttpClient, private mapsService: MapsService) { }

  getUsers(): Observable<any> {
    let subject = new Subject<any>()
    setTimeout(() => { subject.next(this.users); subject.complete(); }, 100)
    return subject;
  }
  getData(): void {
    this.http.get('https://glacial-escarpment-40412.herokuapp.com/users').subscribe(res => {
      this.users.push(res);
    })
  }
  distanceToCenter(lat, lng) {
    const R = 6371;

    const lat1 = this.toRad(this.mapsService.lat);
    const lng1 = this.toRad(this.mapsService.lng);
    const lat2 = this.toRad(lat);
    const lng2 = this.toRad(lng);

    const dLat = lat2 - lat1;
    const dLng = lng2 - lng1;

    const a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
      Math.cos(lat1) * Math.cos(lat2) *
      Math.sin(dLng / 2) * Math.sin(dLng / 2);

    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

    return R * c;
  }
  toRad(degrees) {
    return degrees * Math.PI / 180;
  }
  sortLocations() {
    let sortArray = this.users[0];
    sortArray.sort((a, b) => this.distanceToCenter(a.location.latitude, a.location.longitude) -
      this.distanceToCenter(b.location.longitude, b.location.longitude));
  }

}
