import { Component, OnInit } from '@angular/core';
import {Countries,LocationsService,MapsService,UserService} from '../../shared/index';
import { Observable } from 'rxjs';
import { User} from '../../shared/index'
@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit {
  public lat: number;
  public lng: number;
  public zoom: number;
  public countries: Countries[] = this.locationService.getCountries();
  public users:Observable<any[]>
  constructor(
    private locationService:LocationsService,
    private mapsService:MapsService,
    private userService:UserService
    ) { }

  ngOnInit() {
   
    this.lat = this.mapsService.lat;
    this.lng = this.mapsService.lng;
    this.zoom = this.mapsService.zoom;
    
    this.mapsService.newCoordinators.subscribe(
      (cords:{lat:number , lng:number , zoom:number})=>{
        if (cords){
          this.lat=cords.lat;
          this.lng=cords.lng;
          this.zoom=cords.zoom;
        }
      })
      this.userService.getUsers().subscribe(res=>{
        this.users=res
      })
  }
  onMapClick(e){
    console.log(e);
    
  }

}
